<template>
  <div class="new">
    <van-nav-bar class="nav" title="需求详情" left-arrow @click-left="goback" />
    <p class="headline">{{ content.title }}</p>
    <span class="time">发布时间：{{ content.time }}</span>
    <van-divider dashed />
    <div class="context">
      <p>
        <span class="font-weight-bold">发布企业：</span>{{ content.publisher }}
      </p>
      <p class="mt-8">
        <span class="font-weight-bold">项目简介：</span>
        <span>
          {{ content.intro }}
        </span>
      </p>
      <p class="mt-8">
        <span class="font-weight-bold">项目预算：</span>
        <span>{{ content.budget }}元</span>
      </p>
      <div class="mt-8">
        <span class="font-weight-bold">联系方式：</span><br />
        <div v-if="content.tel">
          <span>联系电话：{{ content.tel }}</span
          ><br />
        </div>
        <span v-if="content.mail">联系邮箱：{{ content.mail }}</span>
      </div>
    </div>
    <van-divider dashed />
    <div class="up">
      <p>
        <span class="lastNext">上一条：</span
        ><a @click="lastAndNext(last.id)" v-if="last">{{ last.title }}</a>
      </p>
      <p class="next">
        <span class="lastNext">下一条：</span>
        <a @click="lastAndNext(next.id)" v-if="next">{{ next.title }}</a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'demandDetail',
  props: ['id'],
  watch: {
    $route() {
      this.getDemand()
    }
  },
  data() {
    return {
      content: {
        title: '',
        publisher: '',
        time: '',
        context: '',
        click: '',
        fileURL: ''
      },
      filename: '',
      enterprise: '',
      year: '',
      subject: '',
      last: {},
      next: {}
    }
  },
  methods: {
    goback() {
      this.$router.push('/demandList')
    },
    async getDemand() {
      this.enterprise = this.$route.query.enterprise
      this.year = this.$route.query.year
      this.subject = this.$route.query.subject
      let res = await this.$http.get(
        '/api/home/readEntry?id=' +
          this.id +
          '&need=' +
          this.subject +
          '&enter=' +
          this.enterprise +
          '&year=' +
          this.year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) return false
      this.content = res.data.data.Enterpriseneeds
      console.log(this.content)
      if (res.data.data.Enterlast) {
        this.last.title = res.data.data.Enterlast.title
        this.last.id = res.data.data.Enterlast.id
      }
      if (res.data.data.Enternext) {
        this.next.title = res.data.data.Enternext.title
        this.next.id = res.data.data.Enternext.id
      }
    },
    lastAndNext(id) {
      this.$router.push({
        path: '/demandList/detail/' + id,
        query: {
          subject: this.subject,
          enterprise: this.enterprise,
          year: this.year
        }
      })
    }
  },
  mounted() {
    this.getDemand()
  }
}
</script>

<style scoped>
.new {
  max-width: 100%;
  overflow: hidden;
}
.nav {
  background-color: #333744;
}
.headline {
  font-size: 1.25rem;
  font-weight: bold;
  margin: 0.5rem;
}
.font-weight-bold {
  font-weight: 600;
}
.time {
  color: #808080;
  margin-top: 0.75rem;
  font-size: 0.8125rem;
  margin-left: 0.5rem;
}
.context {
  margin: 0 0.5rem;
  min-height: 23.125rem;
  line-height: 150%;
}
.lastNext {
  color: #6d6d6d;
}
.next {
  margin-top: 1.25rem;
}
.mt-8 {
  margin-top: 0.8rem;
}
.up {
  margin: 0 0.5rem;
}
</style>
<style>
.van-nav-bar__title {
  color: #fff;
}
.van-nav-bar .van-icon {
  color: #fff;
}
</style>
